export class NewUser {
  public username: string;
  public email: string;
  public password: string;
  public groups: number[];
  public roles: number[];

  constructor(username: string, email: string, password: string, groups: number[], roles: number[]) {
    this.username = username;
    this.email = email;
    this.password = password;
    this.groups = groups;
    this.roles = roles;
  }
}
